<template>
  <v-card width="600" class="mx-auto mt-15">
    <v-row>
      <v-spacer></v-spacer>
      <v-img
        class="mt-10 mb-5"
        lazy-src="https://bcas.lk/wp-content/themes/Divi/images/logo.png"
        max-height="150"
        max-width="250"
        src="https://bcas.lk/wp-content/themes/Divi/images/logo.png"
      ></v-img>
      <v-spacer></v-spacer>
    </v-row>
    <v-divider></v-divider>
    <v-card-text>
      <v-form>
        <span>
          <v-alert outlined type="info" border="left" v-if="!this.hide_info">
            Enter your email address and submit the form to get the password
            reset link.
          </v-alert>
          <v-alert outlined type="error" border="left" v-if="this.has_error">
            A valid email address is required to reset the password!
          </v-alert>
          <v-alert outlined type="success" border="left" v-if="this.has_sent">
            We have sent you an email with a link to reset the password. Please
            check your email.
          </v-alert>
        </span>
        <v-text-field
          label="Email"
          prepend-icon="mdi-account-circle"
          v-model="email"
        />
      </v-form>
    </v-card-text>
    <v-divider></v-divider>
    <v-card-actions>
      <v-spacer></v-spacer>
      <span class="mr-5">
        <router-link to="/login">
          Back to login
        </router-link>
      </span>
      <v-btn color="info" @click="requestResetPassword"
        >Send Password Reset Link</v-btn
      >
    </v-card-actions>
  </v-card>
</template>

<script>
import axios from "axios";

export default {
  name: "ForgotPassword",
  data() {
    return {
      email: null,
      has_error: false,
      hide_info: false,
      has_sent: false
    };
  },
  methods: {
    requestResetPassword() {
      axios.post("/auth/reset-password", { email: this.email }).then(
        result => {
          console.log(result.data.message);
          this.response = result.data;
          this.hide_info = true;
          if (
            result.data.message ==
            "Email could not be sent to this email address."
          ) {
            this.has_error = true;
          } else {
            this.has_error = false;
            this.has_sent = true;
          }
        },
        () => {
          this.has_error = true;
          this.hide_info = true;
        }
      );
    }
  }
};
</script>

<style scoped></style>
